import humanizeDuration from 'humanize-duration';
import React, { useEffect, useState } from 'react';
import { Grid, Autocomplete, Button, TextField, Avatar, Box, Snackbar, Link, Typography } from '@mui/material';
import MuiAlert from '@mui/material/Alert';

import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';

import MainCard from 'ui-component/cards/MainCard';

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div role="tabpanel" hidden={value !== index} id={`simple-tabpanel-${index}`} aria-labelledby={`simple-tab-${index}`} {...other}>
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`
  };
}

async function getChannel(id) {
  if (!localStorage.getItem('login')) return;

  const response = await fetch(`https://api.twitch.tv/helix/users?id=${encodeURIComponent(id.trim())}`, {
    responseType: 'json',
    throwHttpErrors: false,
    headers: {
      Authorization: 'Bearer ' + JSON.parse(localStorage.getItem('login')).access_token,
      'Client-ID': JSON.parse(localStorage.getItem('login')).client_id
    }
  });

  const data = await response.json();

  if (!response.ok) {
    throw new Error(data.message ? `${data.error}, ${data.message}` : response.statusText);
  }

  if (!data.data[0]) {
    throw new Error('Channel not found.');
  }

  return data.data[0];
}

function humanizeLong(ms) {
  return humanizeDuration(Date.now() - ms, {
    units: ['y', 'mo', 'd', 'h', 'm', 's'],
    largest: 4,
    round: true,
    spacer: ' ',
    conjunction: ' and ',
    serialComma: false,
    zero: '0'
  });
}

const Page = () => {
  const [searchResults, setSearchResults] = useState([]);
  const [searchResultsUser, setSearchResultsUser] = useState([]);
  const [game, setGame] = useState({ name: '', id: null, avatarUrl: '' });
  const [title, setTitle] = useState('');
  const [openError, setOpenError] = useState(false);
  const [openSuccess, setOpenSuccess] = useState(false);
  const [errorReason, setErrorReason] = useState('');
  const [successReason, setSuccessReason] = useState('');
  const [channel, setChannel] = useState(null);

  const [outputData, setOutputData] = useState(null);
  const [outputError, setOutputError] = useState(null);

  const [value, setValue] = useState(0);

  const handleSearchChangeID = async (value) => {
    if (value.trim() !== '') {
      const fetchData = async () => {
        try {
          const data = await getChannel(value);
          setOutputError(null);
          setOutputData(data);
        } catch (err) {
          setOutputError(err.message);
          setOutputData(null);
        }
      };
      fetchData();
    }
  };

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  useEffect(() => {
    document.title = 'LsCo Software - Dashboard';
  }, []);

  useEffect(() => {
    if (!channel) return;

    const fetchData = async () => {
      try {
        const data = await getChannel(channel.name.split(' ')[3]);
        setOutputError(null);
        setOutputData(data);
      } catch (err) {
        setOutputError(err.message);
        setOutputData(null);
      }
    };
    fetchData();
  }, [channel]);

  useEffect(() => {
    const fetchData = async () => {
      const loginData = JSON.parse(localStorage.getItem('login'));
      const isLoggedIn = localStorage.getItem('login') !== null;

      if (isLoggedIn) {
        try {
          const responseA = await fetch(`https://api.twitch.tv/helix/channels?broadcaster_id=${loginData.id}`, {
            headers: {
              Authorization: 'Bearer ' + JSON.parse(localStorage.getItem('login')).access_token,
              'Client-ID': JSON.parse(localStorage.getItem('login')).client_id
            }
          });

          const dataA = await responseA.json();

          if (responseA.ok && dataA.data.length > 0) {
            const channelData = dataA.data[0];
            setGame({
              name: channelData.game_name.length ? channelData.game_name : 'None',
              id: channelData.game_id.length ? channelData.game_id : 0,
              avatarUrl: ''
            });
            setTitle(channelData.title);
          }
        } catch (error) {
          console.error(error);
        }
      }
    };

    fetchData();
  }, []);

  const showError = () => {
    setOpenError(true);
  };

  const closeError = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpenError(false);
  };

  const showSuccess = () => {
    setOpenSuccess(true);
  };

  const closeSuccess = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpenSuccess(false);
  };

  const updateProperties = async () => {
    const loginData = JSON.parse(localStorage.getItem('login'));
    if (!loginData) {
      setErrorReason('Log in with your Twitch account to use this tool');
      showError();
      return;
    }

    const broadcasterId = decodeURIComponent(loginData.id);

    try {
      const response = await fetch(`https://api.twitch.tv/helix/channels?broadcaster_id=${broadcasterId}`, {
        method: 'PATCH',
        headers: {
          Authorization: 'Bearer ' + loginData.access_token,
          'Client-ID': loginData.client_id,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          game_id: game.id ?? 0,
          title: title.length ? title : ' '
        })
      });

      if (!response.ok) {
        const data = await response.json();
        setErrorReason(data.message);
        showError();
      } else {
        setSuccessReason('Successfully updated your title & game.');
        showSuccess();
      }
    } catch (error) {
      console.error(error);
    }
  };

  const handleSearchChange = async (value) => {
    if (!localStorage.getItem('login')) {
      return setSearchResults([]);
    }

    if (value.trim() !== '') {
      const response = await fetch(`https://api.twitch.tv/helix/search/categories?query=${decodeURIComponent(value)}&first=15`, {
        headers: {
          Authorization: 'Bearer ' + JSON.parse(localStorage.getItem('login')).access_token,
          'Client-ID': JSON.parse(localStorage.getItem('login')).client_id
        }
      });

      const data = await response.json();

      if (response.ok) {
        setSearchResults(
          data.data.map((category) => ({
            name: category.name,
            id: category.id,
            avatarUrl: category.box_art_url.replace('500', '520')
          }))
        );
      } else {
        setSearchResults([]);
      }
    } else {
      setSearchResults([]);
    }
  };

  const handleSearchChangeUser = async (value) => {
    if (!localStorage.getItem('login')) {
      return setSearchResultsUser([]);
    }

    if (value.trim() !== '') {
      const response = await fetch(`https://api.twitch.tv/helix/search/channels?query=${value}&first=25`, {
        headers: {
          Authorization: 'Bearer ' + JSON.parse(localStorage.getItem('login')).access_token,
          'Client-ID': JSON.parse(localStorage.getItem('login')).client_id
        }
      });

      const data = await response.json();

      if (response.ok) {
        setSearchResultsUser(
          data.data.map((channel) => {
            return {
              name: `${new RegExp('^\\w+$').test(channel.display_name) ? channel.display_name : channel.broadcaster_login} - ID: ${
                channel.id
              }`,
              avatarUrl: channel.thumbnail_url.replace('300x300', '50x50')
            };
          })
        );
      } else {
        setSearchResultsUser([]);
      }
    } else {
      setSearchResultsUser([]);
    }
  };

  return (
    <MainCard title="Dashboard" description="Here you can update the stream or get info about a user.">
      <Box sx={{ width: '100%' }}>
        <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
          <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
            <Tab label="Stream Dashboard" {...a11yProps(0)} />
            <Tab label="Userinfo" {...a11yProps(1)} />
          </Tabs>
        </Box>
        <TabPanel value={value} index={0}>
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <TextField
                fullWidth
                id="standard-basic"
                label="Title"
                variant="filled"
                value={title}
                onChange={(e) => setTitle(e.target.value)}
                autoFocus
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <Autocomplete
                value={game}
                options={searchResults}
                getOptionLabel={(option) => {
                  return option.name;
                }}
                onChange={(event, value) => {
                  setGame(value ?? { name: '', id: null, avatarUrl: '' });
                }}
                isOptionEqualToValue={(option, value) => option.id === value.id}
                renderInput={(params) => (
                  <TextField
                    {...params}
                    fullWidth
                    id="standard-basic"
                    label="Game"
                    variant="filled"
                    onChange={(event) => {
                      handleSearchChange(event.target.value);
                    }}
                  />
                )}
                renderOption={(props, option) => (
                  <li {...props}>
                    <Box display="flex" alignItems="center">
                      <Avatar variant="square" src={option.avatarUrl} alt={option.name} sx={{ width: 35, height: 45, marginRight: 1 }} />
                      {option.name}
                    </Box>
                  </li>
                )}
              />
            </Grid>
            <Grid item>
              <Button variant="text" fullWidth onClick={updateProperties}>
                Update
              </Button>
            </Grid>
          </Grid>
        </TabPanel>
        <TabPanel value={value} index={1}>
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <Autocomplete
                options={searchResultsUser}
                getOptionLabel={(option) => option.name}
                onChange={(event, value) => {
                  setChannel(value);
                }}
                renderInput={(params) => (
                  <TextField
                    {...params}
                    fullWidth
                    id="standard-basic"
                    label="User information"
                    variant="filled"
                    onChange={(event) => {
                      handleSearchChangeUser(event.target.value);
                    }}
                  />
                )}
                renderOption={(props, option) => (
                  <li {...props}>
                    <Box display="flex" alignItems="center">
                      <Avatar src={option.avatarUrl} alt={option.name} sx={{ width: 25, height: 25, marginRight: 1 }} />
                      {option.name}
                    </Box>
                  </li>
                )}
              />
            </Grid>
            <Grid item xs={6}>
              <TextField
                fullWidth
                id="standard-basic"
                label="Channel by ID"
                variant="filled"
                onChange={(event) => {
                  handleSearchChangeID(event.target.value);
                }}
              />
            </Grid>
            <Grid item xs={12}>
              {outputError && (
                <Typography variant="body2" style={{ color: 'red', marginTop: '10px', textAlign: 'center' }}>
                  {outputError}
                </Typography>
              )}
              {!localStorage.getItem('login') && (
                <Typography variant="body2" style={{ color: 'red', marginTop: '10px', textAlign: 'center' }}>
                  <Alert variant="filled" severity="info">
                    Log in with your Twitch account to use this tool
                  </Alert>
                </Typography>
              )}
              {outputData && (
                <Box
                  sx={{
                    backgroundColor: 'rgb(250,250,250,0.1)',
                    borderRadius: '5px',
                    padding: '15px',
                    whiteSpace: 'pre-wrap',
                    maxWidth: '100%',
                    overflowX: 'auto'
                  }}
                >
                  <Typography variant="body2" component="pre">
                    <strong>id:</strong> {outputData.id}
                    <br />
                    <strong>login:</strong> {outputData.login}
                    <br />
                    <strong>display_name:</strong> {outputData.display_name}
                    <br />
                    <strong>type:</strong> {outputData.type === '' ? 'default' : outputData.type}
                    <br />
                    <strong>broadcaster_type:</strong> {outputData.broadcaster_type === '' ? 'default' : outputData.broadcaster_type}
                    <br />
                    <strong>description:</strong> {outputData.description === '' ? 'default' : outputData.description}
                    <br />
                    <strong>profile_image_url:</strong>{' '}
                    {outputData.profile_image_url === '' ? (
                      'default'
                    ) : (
                      <Link target="_blank" href={outputData.profile_image_url}>
                        {outputData.profile_image_url}
                      </Link>
                    )}
                    <br />
                    <strong>offline_image_url:</strong>{' '}
                    {outputData.offline_image_url === '' ? (
                      'default'
                    ) : (
                      <Link target="_blank" href={outputData.offline_image_url}>
                        {outputData.offline_image_url}
                      </Link>
                    )}
                    <br />
                    <strong>created_at:</strong> {outputData.created_at} ({humanizeLong(new Date(outputData.created_at).getTime())} ago)
                  </Typography>
                  <Button
                    variant="contained"
                    color="primary"
                    href={`https://www.twitch.tv/${outputData.login}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    style={{ marginTop: '10px' }}
                  >
                    Visit Channel
                  </Button>
                </Box>
              )}
            </Grid>
          </Grid>
        </TabPanel>
      </Box>

      <Snackbar open={openError} autoHideDuration={2500} onClose={closeError}>
        <Alert onClose={closeError} severity="error" sx={{ width: '100%' }}>
          {errorReason}
        </Alert>
      </Snackbar>

      <Snackbar open={openSuccess} autoHideDuration={1500} onClose={closeSuccess}>
        <Alert onClose={closeSuccess} severity="success" sx={{ width: '100%' }}>
          {successReason}
        </Alert>
      </Snackbar>
    </MainCard>
  );
};

export default Page;
